import "./poster.scss"
import { motion } from "framer-motion"
import { posterFadeInVariants } from "../../motionUtils"
import { BASE_IMG_URL, FALLBACK_IMG_URL } from "../../requests"
import { FaChevronDown, FaMinus, FaPlay, FaPlus } from "react-icons/fa"
import useGenreConversion from "../../hooks/useGenreConversion"
import { showModalDetail } from "../../redux/modal/modal.actions"
import { useDispatch, useSelector } from "react-redux"
import { addToFavourites, removeFromFavourites } from "../../redux/favourites/favourites.actions"
import { Link } from "react-router-dom"
import { selectCurrentUser } from "../../redux/auth/auth.selectors"

const Poster = (result) => {
    const {
        item,
        item: { title, original_name, original_title, name, genre_ids, backdrop_path },
        isFavourite,
    } = result
    let fallbackTitle = title || original_title || name || original_name
    const genresConverted = useGenreConversion(genre_ids)
    const dispatch = useDispatch()
    const currentUser = useSelector(selectCurrentUser)

    const handleAdd = (event) => {
        event.stopPropagation()
        dispatch(addToFavourites({ ...item, isFavourite }))
    }
    const handleRemove = (event) => {
        event.stopPropagation()
        dispatch(removeFromFavourites({ ...item, isFavourite }))
    }

    const handleModalOpening = () => {
        dispatch(showModalDetail({ ...item, fallbackTitle, genresConverted, isFavourite }))
    }

    const handlePlayAction = (event) => {
        event.stopPropagation()
    }

    return (
        <motion.div variants={posterFadeInVariants} className="Poster" onClick={handleModalOpening}>
            {backdrop_path ? (
                <img loading="lazy" src={`${BASE_IMG_URL}/${backdrop_path}`} alt={fallbackTitle} />
            ) : (
                <>
                    <img loading="lazy" src={FALLBACK_IMG_URL} alt={fallbackTitle} />
                    <div className="Poster__fallback">
                        <span>{fallbackTitle}</span>
                    </div>
                </>
            )}
            <div className="Poster__info">
                <div className="Poster__info--iconswrp">
                    <Link className="Poster__info--icon icon--play" onClick={handlePlayAction} to={"/play"}>
                        <FaPlay />
                    </Link>
                    {currentUser &&
                        (!isFavourite ? (
                            <button className="Poster__info--icon icon--favourite" onClick={handleAdd}>
                                <FaPlus />
                            </button>
                        ) : (
                            <button className="Poster__info--icon icon--favourite" onClick={handleRemove}>
                                <FaMinus />
                            </button>
                        ))}
                    <button className="Poster__info--icon icon--toggleModal">
                        <FaChevronDown onClick={handleModalOpening} />
                    </button>
                </div>
                <div className="Poster__info--title">
                    <h3>{fallbackTitle}</h3>
                </div>
                <div className="Poster__info--genres">
                    {genresConverted &&
                        genresConverted.map((genre, index) => (
                            <span key={`Genre--id_${genre}_${index}`} className="genre-title">
                                {genre}
                            </span>
                        ))}
                </div>
            </div>
        </motion.div>
    )
}

export default Poster
